@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    color: #212A3E;
}

body{
    background: url('../public/img/bg.jpg');
    font-family: 'Open Sans', sans-serif;
}

.container {
    max-width: 1200px;
    margin: 1rem auto;
    text-align: center;
    padding: 1rem;
}

.title{
    font-size: 2rem;
    margin: 1rem 0;
}

.input-group {
    width: 100%;
    display: flex;
    margin: 2rem 0;
}

.input-group input{
    width: 100%;
    padding: .7rem;
    border-radius: .5rem 0 0 .5rem;
    border: 0;
    background: rgba(241, 246, 249, 0.5);
}

.input-group input:focus {
    background: rgb(241, 246, 249);
}

.btn-submit {
    border-radius: 0 .5rem .5rem 0;
    padding: .2rem 1rem;
    border: 0;
    font-size: 1rem;
    background: #9BA4B5;
}

.btn-submit:active {
    background: #212A3E;
    color: #F1F6F9;
}

.btn {
    background: #F1F6F9;
    padding: .4rem 1rem;
    border-radius: 1rem;
    border: 0;
}

.btn:active {
    background: #212A3E;
    color: #F1F6F9;
}

.list-group li {
    display: flex;
    justify-content: space-between;
    background: rgba(241, 246, 249, 0.5);
    margin: 0 0 1rem 0;
    padding: .7rem;
    border-radius: 6px;
}

span {
    cursor: pointer;
}
